import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { MoneyService } from '../../shared/money.service';

@IonicPage()
@Component({
  selector: 'page-money',
  templateUrl: 'money.html',
})
export class MoneyPage implements OnInit {
  private isSearchOn: boolean = false;
  private moneyHistory;
  private userMoney;
  private user;
  private moneyOfUser = {
    SoTien: '34,345,909,905',
    TamKhoa: '34,345,909,905',
    CoTheGiaoDich: '34,345,909,905',
    TaiSanRong: '34,345,909,905'
  };
  constructor(public navCtrl: NavController, public navParams: NavParams, private _moneyService: MoneyService) {
  }

  ngOnInit() {
    this.user = JSON.parse(window.localStorage.getItem('user'));
    // console.log(window.localStorage);
    this._moneyService.getMoneyInfo(this.user.userID).subscribe((response: any) => {
      this.userMoney = response;
      console.log(this.userMoney);
    }, error => {
      console.log('Error from api!');
    });

    this._moneyService.getMoneyHistory(this.user.userID).subscribe((response: any) => {
      this.moneyHistory = response;
    });
  }

  toggleDetails() {
    this.isSearchOn = !this.isSearchOn;
  }

  goMenuPage(): void {
    this.navCtrl.setRoot('MenuPage');
  }

  goMoneyInfo() {
    this.navCtrl.push('MoneyInfoPage');
  }

  goMoneyTrans() {
    this.navCtrl.push('MoneyTransPage');
  }

  goMoneyHis() {
    this.navCtrl.push('MoneyHisPage');
  }

  goMoneyWithDraw() {
    this.navCtrl.push('MoneyWithDrawPage');
  }

  goMerchangdiseTrans() {
    this.navCtrl.push('MerchandiseTransPage');
  }
}



